import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { VendorLayoutComponent } from './vendor-layout/vendor-layout.component';
import { ListVendorComponent } from './list-vendor/list-vendor.component';
import { EditVendorComponent } from './edit-vendor/edit-vendor.component';
import { ViewVendorComponent } from './view-vendor/view-vendor.component';
import { AddVendorComponent } from './add-vendor/add-vendor.component';

const routes: Routes = [
  {
    path: '', component: VendorLayoutComponent,

    children: [
      {
        path: 'listVendor',
        component: ListVendorComponent
      },
      {
        path: 'editVendor/:vendorId',
        component: EditVendorComponent
      },
      {
        path: 'viewVendor/:vendorId',
        component: ViewVendorComponent
      },
      {
        path: 'addVendor',
        component: AddVendorComponent
      },


    ]
  }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VendorsRoutingModule { }
