import { forEach } from '@angular/router/src/utils/collection';
import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../utilities/imageupload'
import { Global } from '../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

import { ExportExcelService } from '../export-excel.service';
import { Export2Excel } from '../../../utilities/export2Excel';
declare var jsPDF: any;
declare var XLSX: any;
@Component({
  providers: [Beyond, Export2Excel],
  selector: 'app-list-vendor',
  templateUrl: './list-vendor.component.html',
  styleUrls: ['./list-vendor.component.scss']
})
export class ListVendorComponent implements OnInit {
  loginData: any;
  vendorListDetails: any = [];
  vendorDetailsForExcelExport: any = [];
  vendorListResponse: boolean;
  All: boolean;
  searchVendor: FormGroup;
  vendor: any;
  VendorId: boolean;
  VendorName: boolean;
  AccountNumber: boolean;
  PhoneNumber: boolean;
  Address: boolean;
  RepEmailAddress: boolean;
  Status: boolean;
  proId: any;
  displayFileModal = 'none';
  filesToUpload: any;
  progressShow: boolean = false;
  progressHide: boolean = false;
  percentage: any;
  excelData: string;
  excelError: boolean;
  excelfile: boolean;
  vendorDetailsForExcelDownload: any = [{
    vendorName: "",
    taxIdOrEIN: "",
    accountRepName: "",
    eligible1099: "",
    vendorCategory: "",
    repEmailAddress: "",
    accountNumber: "",
    phoneNumber: "",
    extensionNumber: "",
    streetAddress: "",
    unit: "",
    city: "",
    state: "",
    zipCode: "",
    status: "",
    specialNotes: "",
  }];
  vendorList: any;
  constructor(public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    public export2Excel: Export2Excel,
    private excelService: ExportExcelService) {

  }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proId = this.loginData.loginCode;
    this.getVendorList()
    this.All = true;
    {
      this.searchVendor = this.formBuilder.group({
        vendorId: [null],
        vendorName: [null],
        accountNumber: [null],
        phoneNumber: [null],
        repEmailAddress: [null],
        streetAddress: [null],
        status: [null],
        selectBy: [null]
      })
    }

  }
  vendorSelection(x) {
    console.log("x" + x)
    if (x == "All") {
      this.getVendorList()
    }


    if (x == "VendorId") {
      this.VendorId = true;
      this.VendorName = false;
      this.AccountNumber = false;
      this.PhoneNumber = false;
      this.Address = false;
      this.RepEmailAddress = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "VendorName") {
      this.VendorId = false;
      this.VendorName = true;
      this.AccountNumber = false;
      this.PhoneNumber = false;
      this.Address = false;
      this.RepEmailAddress = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "Address") {
      this.VendorId = false;
      this.VendorName = false;
      this.AccountNumber = false;
      this.PhoneNumber = false;
      this.Address = true;
      this.RepEmailAddress = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "RepEmailAddress") {
      this.VendorId = false;
      this.VendorName = false;
      this.AccountNumber = false;
      this.PhoneNumber = false;
      this.Address = false;
      this.RepEmailAddress = true;
      this.Status = false;
      this.All = false;
    }
    if (x == "PhoneNumber") {
      this.VendorId = false;
      this.VendorName = false;
      this.AccountNumber = false;
      this.PhoneNumber = true;
      this.Address = false;
      this.RepEmailAddress = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "Status") {
      this.VendorId = false;
      this.VendorName = false;
      this.AccountNumber = false;
      this.PhoneNumber = false;
      this.Address = false;
      this.RepEmailAddress = false;
      this.Status = true;
      this.All = false;
    }
    if (x == "AccountNumber") {
      this.VendorId = false;
      this.VendorName = false;
      this.AccountNumber = true;
      this.PhoneNumber = false;
      this.Address = false;
      this.RepEmailAddress = false;
      this.Status = false;
      this.All = false;
    }
  }

  reset() {
    this.searchVendor.reset();
    this.getVendorList()
  }



  // Search Customer Starts
  searchVendorData() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let obj = {
      "vendorId": this.searchVendor.value.vendorId,
      "vendorName": this.searchVendor.value.vendorName,
      "accountNumber": this.searchVendor.value.accountNumber,
      "phoneNumber": this.searchVendor.value.phoneNumber,
      "repEmailAddress": this.searchVendor.value.repEmailAddress,
      "streetAddress": this.searchVendor.value.streetAddress,
      "status": this.searchVendor.value.status,
    }
    console.log(obj)
    this.http.post(Global.url + '/searchVendor/' + this.proId, [
      { id: 'vendorId', value: obj.vendorId },
      { id: 'vendorName', value: obj.vendorName },
      { id: 'accountNumber', value: obj.accountNumber },
      { id: 'phoneNumber', value: obj.phoneNumber },
      { id: 'repEmailAddress', value: obj.repEmailAddress },
      { id: 'streetAddress', value: obj.streetAddress },
      { id: 'status', value: obj.status }
    ]).subscribe((data: any) => {
      this.vendor = data.json();
      console.log("center" + JSON.stringify(this.vendor))
      if (this.vendor.status == 200) {
        this.vendorListDetails = this.vendor.response;
        this.vendorListResponse = true;
        console.log("Vendor values" + JSON.stringify(this.vendorListDetails))
        this.searchVendor.reset();
      } else {
        this.vendorListResponse = false;
        console.log('The else')
      }
    })
  }

  getVendorList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfVendors' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.vendorListDetails = response.response;
          this.vendorListResponse = true;
        }
        else {
          this.vendorListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  addVendor() {
    this.router.navigate(['/vendor/addVendor'])
  }
  viewVendor(data) {
    this.router.navigate(['/vendor/viewVendor', data.vendorId])
  }
  editVendor(data) {
    this.router.navigate(['/vendor/editVendor', data.id])
  }
  deleteInventory() {
  }


  onCloseHandled() {
    this.displayFileModal = 'none';
  }


  exportAsXLSX(): void {
    this.vendorDetailsForExcelExport = []
    this.vendorListDetails.forEach((e) => {
      this.vendorDetailsForExcelExport.push({
        vendorName: e.vendorName,
        taxIdOrEIN: e.taxIdOrEIN,
        accountRepName: e.accountRepName,
        eligible1099: e.eligible1099,
        vendorCategory: e.vendorCategory,
        repEmailAddress: e.repEmailAddress,
        accountNumber: e.accountNumber,
        phoneNumber: e.phoneNumber,
        extensionNumber: e.extensionNumber,
        streetAddress: e.streetAddress,
        unit: e.unit,
        city: e.city,
        state: e.state,
        zipCode: e.zipCode,
        status: e.status,
        specialNotes: e.specialNotes,
      })
    })
    this.excelService.exportAsExcelFile(this.vendorDetailsForExcelExport, 'ExportVendor');
  }


  downloadFile(): void {
    this.excelService.exportAsExcelFile(this.vendorDetailsForExcelDownload, 'DownloadVendor');
  }


  openImport() {
    this.progressShow = false;
    this.progressHide = false;
    event.preventDefault();
    event.stopPropagation();
    this.displayFileModal = 'block';
  }
  closeImport() {
    this.displayFileModal = 'none';
    this.excelError = false;
    this.excelfile = false;
    this.select('')
  }

  select(event) {
    this.excelError = false;
    this.excelfile = false;
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      console.log("TTT" + event.target.files[0].type)
      this.excelError = true;
      this.excelfile = false;
      if (event.target.files[0].type === "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet") {
        this.excelData = event.target.files[0].type
        console.log("excelData" + this.excelData)
        var file = event.target.files[0];
        this.progressShow = false;
        this.progressHide = false;
        this.filesToUpload = event.target.files[0];
      } else {
        this.excelError = false;
        this.progressShow = false;
        this.progressHide = false;
        this.excelfile = true;
        // alert("Please Select an Excel FILE");
      }
    }
  }
  checking() {
    if (this.excelData === "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet") {
      this.import('')

    }
    else {
      // alert("Please Select an Excel FILE");
      this.excelfile = true;
    }

  }

  import($event) {
    console.log("excelData" + JSON.stringify(this.excelData))
    const url = Global.url + '/vendorImport' + '/' + this.loginData.loginCode

    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append('file', this.filesToUpload)
    const config = {
      headers: {
        'content-type': 'multipart/form-data'
      }
    }
    xhr.open('post', url, true);
    xhr.upload.onprogress = (e) => {
      if (e.lengthComputable) {
        this.percentage = (e.loaded / e.total) * 100;
        this.progressShow = true;
        this.progressHide = false;
        if (this.percentage == 100) {
          this.toastr.success('Data imported Successfully...')
          //location.reload();
          this.progressHide = true;
          this.displayFileModal = 'none';
          this.getVendorList()
        }
      }
    }
    xhr.onerror = function (e) {

    };
    xhr.onload = function () {

    };
    xhr.send(formData);
  }



}
