import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond'
import { Router, ActivatedRoute } from '@angular/router';
import { Global } from '../../../global/global';
import { HttpClient } from '@angular/common/http';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';


@Component({
  providers: [HttpClient, Beyond],
  selector: 'app-view-vendor',
  templateUrl: './view-vendor.component.html',
  styleUrls: ['./view-vendor.component.scss']
})
export class ViewVendorComponent implements OnInit {
  viewDataValues: any;
  vendorId: any;
  vendorData: any;
  dataVendor: boolean = false;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    public httpClient: HttpClient,
    private spinner: NgxSpinnerService,
  ) { }

  ngOnInit() {
    this.vendorId = this.route.snapshot.params['vendorId'];
    this.beyond.beyondAdmin()
    this.viewDataValues = JSON.parse(localStorage.getItem("viewData"))
    console.log("viewDataValues" + JSON.stringify(this.viewDataValues))
    this.viewVendor();
  }

  okButton() {
    this.router.navigate(['vendor/listVendor'])
  }

  editVendor() {
    this.router.navigate(['vendor/editVendor', this.vendorId])
  }

  // start view Vendor
  viewVendor() {
    this.spinner.show()
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewVendors/' + this.vendorId, options)
      .subscribe(data => {
        var vendor = data.json();
        if (vendor.status == 200) {
          this.spinner.hide()
          this.vendorData = vendor.response[0];
          if (this.vendorData.eligible1099 == 'true' ||
            this.vendorData.eligible1099 == 'TRUE') this.vendorData.eligible1099 = true

          if (this.vendorData.eligible1099 == "" ||
            this.vendorData.eligible1099 == 'false' ||
            this.vendorData.eligible1099 == 'FALSE') this.vendorData.eligible1099 = false
          console.log("vendor Data", JSON.stringify(this.vendorData))
          this.dataVendor = true;
        } else {
          this.dataVendor = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  // end view Vendor
}
