import { Component, OnInit, ViewChild } from "@angular/core";
import { Beyond } from "../../../utilities/beyond";
import { Router, ActivatedRoute } from "@angular/router";
import { ImageUpload } from "../../../utilities/imageupload";
import { Global } from "../../../global/global";
import { Http, Headers, RequestOptions } from "@angular/http";
import { ToastrService } from "ngx-toastr";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
  FormArray
} from "@angular/forms";
import { CustomValidators } from '../../../utilities/customValidators';
@Component({
  providers: [Beyond, CustomValidators],
  selector: 'app-edit-vendor',
  templateUrl: './edit-vendor.component.html',
  styleUrls: ['./edit-vendor.component.scss']
})
export class EditVendorComponent implements OnInit {
  editVendor: FormGroup;
  loginEmailData: any;
  prozData: any;
  loginData: any;
  vendorId: any;
  vendors: boolean;
  vendorList: any;
  vendorData: any;
  submitted: string;
  statesData: any;
  dataVendor: boolean;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"));
    this.prozData = this.loginData.loginCode;
    this.loginEmailData = this.loginData.emailId;
    this.vendorId = this.route.snapshot.params['vendorId'];
    this.editVendor = this.formBuilder.group({
      vendorId: [''],
      proId: [''],
      vendorName: ['', Validators.required],
      taxIdOrEIN: [''],
      accountRepName: [''],
      eligible1099: [''],
      vendorCategory: [''],
      repEmailAddress: [''],
      accountNumber: [''],
      phoneNumber: [''],
      extensionNumber: [''],
      streetAddress: [''],
      unit: [''],
      state: [''],
      city: [''],
      zipCode: [''],
      status: [''],
      specialNotes: [''],
    })
    this.listVendorCategory();
    this.viewVendor();
    this.getListOfStates();
  }

  cancel() {
    this.router.navigate(['/vendor/listVendor'])
  }

  get f() {
    return this.editVendor.controls
  }

  keyPress1(event: any) {
    const pattern = /^[a-zA-Z ]+$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && event.keyCode != 9 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  //STATES

  getListOfStates() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + "/getListOfStates", options).subscribe(
      data => {
        var response = data.json();
        this.statesData = response.response;
      },
      error => {
        this.toastr.warning("Unable to connect to server");
      }
    );
  }
  //STATES
  onSubmit() {
    this.submitted = "true";
    if (!this.editVendor.value.vendorName) {
      return
    }
    else {
      this.saveVendor()
    }
  }
  saveVendor() {
    let editObj = {
      "vendorId": this.vendorId,
      "proId": this.prozData,
      "vendorName": '' + this.editVendor.value.vendorName,
      "taxIdOrEIN": '' + this.editVendor.value.taxIdOrEIN,
      "accountRepName": '' + this.editVendor.value.accountRepName,
      "eligible1099": '' + this.editVendor.value.eligible1099,
      "vendorCategory": '' + this.editVendor.value.vendorCategory,
      "repEmailAddress": '' + this.editVendor.value.repEmailAddress,
      "accountNumber": '' + this.editVendor.value.accountNumber,
      "phoneNumber": '' + this.editVendor.value.phoneNumber,
      "extensionNumber": '' + this.editVendor.value.extensionNumber,
      "streetAddress": '' + this.editVendor.value.streetAddress,
      "unit": '' + this.editVendor.value.unit,
      "city": '' + this.editVendor.value.city,
      "state": '' + this.editVendor.value.state,
      "zipCode": '' + this.editVendor.value.zipCode,
      "status": '' + this.editVendor.value.status,
      "specialNotes": '' + this.editVendor.value.specialNotes,
      "modifiedBy": this.loginData.emailId
    }

    console.log("ADD OBJ" + JSON.stringify(editObj))
    this.http.post(Global.url + '/updateVendors', editObj)
      .subscribe(data => {
        var vendorData = data.json();
        console.log("customerData" + JSON.stringify(vendorData))
        if (vendorData.status == 200) {
          this.router.navigate(['/vendor/listVendor'])
        }

      }, error => {
        console.log(error);// Error getting the data
      });
  }


  // LIST Vendor Category STARTS
  listVendorCategory() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getVendorCategory/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var vendors = data.json();
        if (vendors.status == 200) {
          console.log("Category Details" + JSON.stringify(vendors))
          this.vendorList = vendors.response;
          console.log("list" + JSON.stringify(this.vendorList))
          // return false;
          // this.vendors = true;
        } else {
          // this.vendors = false;
        }
      })
    // , error => {
    // //  this.toastr.warning('Unable to connect to server');
    // })
  }
  // LIST Vendor Category ENDS


  // start view Vendor
  viewVendor() {
    //this.spinner.show()
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewVendors/' + this.vendorId, options)
      .subscribe(data => {
        var vendor = data.json();
        if (vendor.status == 200) {
          // this.spinner.hide()
          this.vendorData = vendor.response[0];
          if (this.vendorData.eligible1099 == 'true' ||
            this.vendorData.eligible1099 == 'TRUE') this.vendorData.eligible1099 = true

          if (this.vendorData.eligible1099 == "" ||
            this.vendorData.eligible1099 == 'false' ||
            this.vendorData.eligible1099 == 'FALSE') this.vendorData.eligible1099 = false
          console.log("vendor Data", JSON.stringify(this.vendorData))
          this.dataVendor = true;
        }
        else {
          this.dataVendor = false;
        }

        this.editVendor = this.formBuilder.group({
          //"vendorId": this.vendorId,
          //"proId": this.prozData,
          "vendorName": [this.vendorData.vendorName, Validators.required],
          "taxIdOrEIN": [this.vendorData.taxIdOrEIN, Validators.required],
          "accountRepName": [this.vendorData.accountRepName, Validators.required],
          "eligible1099": [this.vendorData.eligible1099, Validators.required],
          "vendorCategory": [this.vendorData.vendorCategory, Validators.required],
          "repEmailAddress": [this.vendorData.repEmailAddress, Validators.required],
          "accountNumber": [this.vendorData.accountNumber, Validators.required],
          "phoneNumber": [this.vendorData.phoneNumber, Validators.required],
          "extensionNumber": [this.vendorData.extensionNumber, Validators.required],
          "streetAddress": [this.vendorData.streetAddress, Validators.required],
          "unit": [this.vendorData.unit, Validators.required],
          "city": [this.vendorData.city, Validators.required],
          "state": [this.vendorData.state, Validators.required],
          "zipCode": [this.vendorData.zipCode, Validators.required],
          "status": [this.vendorData.status, Validators.required],
          "specialNotes": [this.vendorData.specialNotes, Validators.required],
        });


      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })


  }
  // end view Vendor

}
